.App {

}

body {
  background-color: black
}

.date-form {

}

.graph {
  float: left;
  height:600px;
  background-color: LightSlateGray;
  border-right: solid black 2px;
  border-bottom: solid black 2px;
}

.dash-panel {
  height:600px;
}

.graph-options {
  float: left;
  height: 600px;
  background-color: black;
  width: 250px;
  border-right: solid black 4px;

}

.chart-title {
  text-align: left;
  margin-left: 250px;
  color:white;
}

.xaxis {
  color:white;
  font-size:14px;
}

.yaxis {
  color:white;
  font-size:12px;
}

.yaxis-label {
  fill:white;
}

.drawer {
  border-bottom: solid black 2px;
  border-right: solid black 2px;
  border-left: solid black 2px;
  width: 100%;

}

.drawer-title {
  background-color: LightSlateGray;
  color: white;
  text-align: left;
  padding-top: 5px;
  width: 100%;
  height: 25px;


}

.drawer-title-black {
  background-color: black;
  color: white;
  text-align: left;
  padding-top: 5px;
  width: 100%;
  height: 25px;
}

.drawer-date {
  background-color: LightSlateGray;
  color: white;
  text-align: left;

  padding-top: 5px;
  width: 100%;
  height: 25px;
  border-bottom: solid black 2px;
  border-left: solid black 2px;
  border-right: solid black 2px;
}

.drawer-date-text {
  padding-left: 10px;
}

.drawer-date-input {
  float: right;
  margin-right: 20px;
  border: none;
  border-bottom: 1px solid white;
  background-color: LightSlateGray;
  font-size: 14px;
  color: white;
  outline: none;
}


.drawer-submit-button {
  background-color: LightSlateGray;
  color: white;
  text-align: left;
  padding-top: 5px;
  width: 100%;
  height: 25px;
  border-bottom: solid black 2px;
  border-left: solid black 2px;
  border-right: solid black 2px;

}

.cabinet {
  width:100%;
}

.report-options {
  width: 100%;
  overflow-x: scroll;
  height:200px;
  background-color: LightSlateGray;
  border-bottom: solid black 2px
}

.options {
  width: 100%;
  overflow-x: scroll;
  height:440px;
  background-color: LightSlateGray;
  border-bottom: solid black 2px
}

.option-list {
  margin: 0px;
  padding: 0px;
  list-style: none;
  width: 100%;
  display: inline-block;
  overflow-x: scroll;
  bottom:0px;
}

.option-list-item {
  float:left;
  width: 100%; 
  background-color: LightSlateGray;
  padding-top:5px;
  padding-bottom:5px;
}

.item-checkbox {
  float:left;
}

.item-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  margin-left:10px;
  margin-top:25%;
  height: 10px;
  width: 10px;
  background-color: LightSlateGray;
  border-radius: 50%;
}

.item-text {
  padding-left: 20px;
  float:left;
  width: 80%;
  color: white;
}

.line-label {
  color:white;
}

.drawer:hover .drawer-title{
  background-color: black;
}

.drawer-date:hover {
  background-color: black;
}

.drawer-date:hover .drawer-date-input {
  background-color: black;
}

.drawer-submit:hover .drawer-submit-button{
  background-color: black;
}

.option-list-item:hover {
  background-color: black;
}

.option-list-item:hover .checkmark{
  background-color: black;
}

.item-checkbox input:checked ~ .checkmark {
  background-color: white;
}



::placeholder {
  color:white;
};




